import BootPay from 'bootpay-js';
import { BootPayApi } from '../../../Api';

let BootpayRest = require('bootpay-rest-client');
BootpayRest.setConfig('5edb7b5c8f0751002bfcd4bf', '/mKKFkSwJ/N7RJ5Hpb96YbvzVZA+VH+knKrGq4HD6zU=');

export const bootPayRequest = form => {
  const { book, user, bookId, orderId, buyMyBook, studentId } = form;
  BootPay.request({
    price: bookId === '9859a212-c2db-972e-1b27-d68a3fce33f1' ? 0 : book.price, //  
    application_id: '5edb7b5c8f0751002bfcd4bc',
    name: book.name, //  
    pg: bookId === '9859a212-c2db-972e-1b27-d68a3fce33f1' ? 'danal' : 'inicis',
    method: bookId === '9859a212-c2db-972e-1b27-d68a3fce33f1' ? 'card_rebill' : '', //,      .
    show_agree_window: 1, //      
    items: [
      {
        item_name: book.name, //
        qty: 1, //
        unique: bookId, //   primary key
        price: book.price, // 
      },
    ],
    user_info: {
      username: user.name,
      email: user.email,
      phone: user.phone,
    },
    order_id: orderId, // ,    .
    account_expire_at: '2018-05-25', //    ( yyyy-mm-dd  .  . )
    extra: {
      vbank_result: 1, //   ,   (1), (0),  (1)
      quota: '0,2,3', //  5      , [0(), 2, 3] ,  12 
    },
  })
    .error(function(data) {
      //    .
      console.log(data);
    })
    .cancel(function(data) {
      //  .
      console.log(data);
    })
    .ready(function(data) {
      //      .
      console.log(data);
    })
    .confirm(function(data) {
      //   ,     .
      // -       .
      console.log(data);
      var enable = true; //       
      if (enable) {
        BootPay.transactionConfirm(data); //     .
      } else {
        BootPay.removePaymentWindow(); //         .
      }
    })
    .close(function(data) {
      //   . (,,   )
      console.log(data);
    })
    .done(function(data) {
      //   
      //        .
      if (bookId === '9859a212-c2db-972e-1b27-d68a3fce33f1') {
        console.log(data);
        BootpayRest.getAccessToken().then(function(token) {
          if (token.status === 200) {
            BootPayApi.post('/subscribe/billing', {
              billing_key: data.billing_key,
              item_name: book.name,
              price: book.price,
              order_id: new Date().getTime(),
              items: [
                {
                  item_name: book.name,
                  qty: 1,
                  unique: '9859a212-c2db-972e-1b27-d68a3fce33f1',
                  price: 5900,
                },
              ],
            }).then(function(response) {
              response.header('Access-Control-Allow-Origin', '*');
              if (response.status === 200) {
                let formValue = {
                  studentId: studentId * 1,
                  isSubscribing: true,
                };
                buyMyBook(formValue, () => {
                  window.sessionStorage.setItem('isSubscribing', true);
                  console.log('update membership complete');
                  form.close();
                });
              }
            });
          }
        });
      } else {
        let formValue = {
          bookId: bookId,
          receiptId: data.receipt_id,
          studentId: studentId,
        };
        buyMyBook(formValue, () => console.log('complete'));
      }
    });
};
